.SearchResults {
  width: 50%;
  height: 950px;
  overflow-y: scroll;
  padding: .88rem;
  background-color: rgba(1, 12, 63, 0.7);
  -webkit-box-shadow: 0 4px 2px 2px #000000;
          box-shadow: 0 4px 2px 2px #000000;
}

::-webkit-scrollbar {
    width: 0px;
    background: transparent;
}

@media only screen and (max-width: 1020px) {
  .SearchResults {
    width: 90%;
    margin-bottom: 2rem;
  }
}

.SearchResults h2 {
  border-bottom: 1px solid #6f6f6f;
  padding: 1.11rem 1.11rem;
}



